import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import Bg3 from '../images/img.PNG'

const Header = () => {
    return (
        <Section id="1">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12 text-start">
                        <div className="header-text">
                            <h1>Kannas<br /> Textile</h1>
                            <p>Kannas textile- это сервис, который предлогает клиенту реалистические ожидания, а затем эти ожидания оправдывают себя, принося радость и удовлетворение клиентам.</p>
                        </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                        <img src={Bg3} alt="" className="img-fluid" />
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
.row{
    display: flex;
    justify-content: center;
    align-items: center;
}
    .header-text{
            letter-spacing: 0.05em;
            line-height: 5em;
            
        h1{
            font-size: 95px;
            font-weight: bold;
            font-family: 'Andada Pro', serif;
            
        }   
        p{
            line-height: 1.5em;
            font-size: 20px;
            color: grey;
            font-family: 'Andada Pro', serif;
        }
        a{
            color: #FF7C59;
            font-size: 18px;
        }
        @media (max-width: 500px){
            width: 100%;
            margin: auto;
            padding-top: 2.5em;
            text-align: center;
            h1{
                font-size: 45px;
            }
            p{
                font-size: 20px;
                width: 100%;
                text-align: justify;
            }
            a{
                font-size: 15px;
            }
        }
    }
`
export default Header
