import React from 'react'
import styled from 'styled-components'
import Bg1 from '../images/man.jpg'
import Bg2 from '../images/man1.jpg'
import Bg3 from '../images/man2.jpg'
import Bg4 from '../images/man3.jpg'


const About = () => {
    return (
        <Section id="2">
            <div className="banner">
                <div className="container">
                    <div className="title">
                        <h2 className="text-center">О нас</h2>
                        <p className="text-center mt-5">В нашем швейном производстве Kannas textile - мы предлогаем широкий спектр услуг текстиля для HoReCa. В основном мы специализируемся на пошиве декоративных изделий ( фартуки, униформа, скатерти-салфетки, постельное бельё и подушки) мы сотрудничаем с ресторанами и кафе, гостиницами и медучреждениями и т.д.
                            Занимаемся проектированием и пошивом полезной одежды;
                            Поможем создать цельный визуальный образ вашего заведения, и подберём единый корпоративный стиль.
                            Также мы предлогаем услуги производства серийной одежды и производства текстиля.
                            Образцы готовых изделий Вы можете посмотреть подъехав к нам в офис. </p>
                    </div>
                    <div className="row align-items-center">
                        <div className="col-lg-3 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="item1">
                                    <img src={Bg1} alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="item1">
                                    <img src={Bg2} alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="item1">
                                    <img src={Bg3} alt="" />
                                </div>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6 col-sm-12">
                            <div className="card">
                                <div className="item1">
                                    <img src={Bg4} alt="" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Section>
    )
}
const Section = styled.div`
    padding: 5em 0;
    .row{
        overflow: hidden;
    }
    .title{      
    h2{
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-family: 'Andada Pro', serif;
        font-size: 30px;
        color: #000;
        @media (max-width: 500px){
            font-size: 23.5px;
        }
    }
    p{
            line-height: 1.5em;
            font-size: 25px;
            color: black;
            font-family: 'Andada Pro', serif;
     :after {
    border-top: 3px solid #e71f30;
    display: block;
    width: 81px;
    content: "";
    margin: 8px auto 0;
}
@media (max-width: 500px){
    font-size: 20px;
    text-align: justify !important;
}

    }
    }
    .card{
        width: 18rem;
        border: none;
        img{
            width: 100%;
            height:60vh;
            object-fit: contain;
        }
        @media (max-width: 500px){
            width: 100%;
            margin: 0 auto;
        }
    }
    .card2{
        width: 18rem;
        border: none;
        float: right;
        img{
            width: 100%;
            height:60vh;
            object-fit: contain;
        }
        @media (max-width: 500px){
            width: 100%;
            margin: 0 auto;
        }
    }
    .img-fluid{
        @media (max-width: 500px){
            display: none;
        }
    }
`
export default About
