import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

const Contact = () => {
    return (
        <Section id="4">
            <div className="container">
                <h2 className="title">Связаться</h2>
                <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12 text text-center my-5">
                        <h4>Насриддинова Шахзода</h4>
                        <p>Публицист</p>

                        <p>123-456-7890</p>
                        <Link className="">info@my-domain.com</Link>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12 text text-center my-5 text1">
                        <h4>Насриддинова Шахзода</h4>
                        <p>Агент</p>

                        <p>123-456-7890</p>
                        <Link className="">info@my-domain.com</Link>
                    </div>
                    <i class="far minee fa-edit adit fa-2x"></i>
                </div>
                <h3 className="form-title">Контакт</h3>
                <form action="#" className="contact-form m-auto py-5">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <input type="text" className="form-control" placeholder="Имя:" />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <input type="text" className="form-control" placeholder="Фамилия:" />
                                    </div>
                                </div>
                            </div>
                            <div class="form-group my-3">
                                <textarea name="" id="" cols="30" rows="5" className="form-control" placeholder="Комментария:"/>
                            </div>
                                <a href="#" className="btn  text-center m-auto">Отправить</a>
                </form>
               
            </div>
        </Section>
    )
}
const Section = styled.div`
.row{
    i{
    text-align: end;
}
}
    .title{
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 1px;
        font-family: 'Andada Pro', serif;
        font-size: 30px;
        color: #000;
        @media (max-width: 500px){
            font-size: 23.5px;
        }
    }
    .text{       
        border-right: 2px solid black;
        @media (max-width: 500px){
            border: none;
        }
        h4{
            text-align: center;
            text-transform: uppercase;
            letter-spacing: 1px;
            font-family: 'Andada Pro', serif;
            font-size: 18px;
            color: #000;
        }
        p{
            text-align: center;
            text-transform: uppercase;
            letter-spacing: 1px;
            font-family: 'Andada Pro', serif;
            font-size: 14px;
            color: #000;
        }
        a{
            margin: 0 auto !important;
            text-transform: uppercase;
            letter-spacing: 1px;
            font-family: 'Andada Pro', serif;
            font-size: 12px;
            color: #000;
            text-decoration: none;
        }
    }
    .text1{
        border-right: none;
        @media (max-width: 500px){
            display: none;
        }
    }
    form{
        max-width: 50%;
        @media (max-width: 500px){
            max-width: 90%;
        }
    }
    .form-title{
        margin: 2em 0 0 0;
        font-weight: bold;
        font-size: 18px;
        font-family: 'Andada Pro', serif;
        text-align: center;
    }
    .form-group{
        input{
            border-top: none;
            border-left: none;
            border-right: none;
            border-bottom: 3px solid #FF7C59;
        }
        textarea{
            border-top: none;
            border-left: none;
            border-right: none;
            border-bottom: 1px solid black;
        }

    }
        .btn{
            color: #FF7C59;
           @media (max-width: 500px){
               width: 8rem;
               font-size: 12px;
           }
        }
`
export default Contact
