import React from 'react'
import styled from 'styled-components'
import { Link } from 'react-router-dom'
import Home from './Home'   

const LoginIn = () => {
    function hi(e){
        e.preventDefault()
    }
    return (
        <Section>
            <Link to="/" className="text-light"><i class="fal fa-arrow-alt-circle-left fa-2x ms-5 mt-5"></i></Link>
            <div className="center">
                <h1>Login</h1>
                <form action="">
                    <div className="txt_field">
                        <input type="text" required/>
                        <label htmlFor="">Username</label>
                    </div>
                    <div className="txt_field">
                        <input type="password" required/>
                        <span></span>
                        <label htmlFor="">Password</label>
                    </div>
                    <div className="pass">Forgot Password?</div>
                    <input className="inputs" type="submit" value="Login" />
                    <div className="signup_link">
                        Not a member? <Link onClick={(e) => hi(e)} to="/admin">Sign up</Link>
                    </div>
                </form>
            </div>
             {/* <section id="login">
    <div class="container">
      <div class="row">
        <div class="col-md-6 mx-auto">
          <div class="card">
            <div class="card-header">
              <h4>Account Login</h4>
            </div>
            <div class="card-body">
              <form action="index.html">
                <div class="form-group">
                  <label for="email">Email</label>
                  <input type="text" class="form-control"/>
                </div>
                <div class="form-group">
                  <label for="password">Password</label>
                  <input type="password" class="form-control"/>
                </div>
                <input type="submit" value="Login" class="btn btn-primary btn-block"/>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section> */}
        </Section>
    )
}
const Section = styled.div`
     font-family: monospace;
     background: linear-gradient(120deg, #2980b9,#8e44ad);
     height: 100vh;
     overflow: hidden;
     i{
         &:hover{
             color: black;
         }
     }
     .center{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 400px;
        background: #fff;
        border-radius: 10px;
        h1{
            text-align:center;
            padding: 0 0 20px 0;
            border-bottom: 1px solid silver;
        }
        @media (max-width: 500px){
                        width: 90%;
                    }
        form{
            padding: 0 40px;
            box-sizing: border-box;
            
            .txt_field{
                position: relative;
                border-bottom: 2px solid #adadad;
                margin: 30px 0;
                input{
                    width: 100%;
                    padding: 0 5px;
                    height: 40px;
                    font-size: 16px;
                    border: none;
                    background: none;
                    outline: none;
                }
                label{
                    position: absolute;
                    top: 50%;
                    left: 5px;
                    color: #adadad;
                    transform: translateY(-50%);
                    font-size: 16px;
                    pointer-events: none;
                    transition: .5s;
                }
                span :before{
                    content: '';
                    position: absolute;
                    top: 40px;
                    left: 0;
                    width: 0%;
                    height: 2px;
                    background: #2691d9;   
                    transition: .5s;
                }
                input:focus ~ label,
                input:valid ~ label{
                    top: -5px;
                    color: #2691d9;
                }
                input:focus ~ ::before,
                input:valid ~ ::before{
                    width: 100%;
                }
                .poss{
                    margin: -5px 0 20px 5px;
                    color: #a6a6a6;
                    cursor: pointer;
                    &:hover{
                        text-decoration: underline;

                    }
                }
             
                }             
                .signup_link{
                    margin: 30px 0;
                    text-align: center;
                    font-size: 16px;
                    color: #666;
                    a{
                        color: #2691d9;
                        text-decoration: none;
                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
            }
            .inputs{
                    width: 100%;
                    height: 50px;
                    border: 1px solid;
                    background: #2691d9;
                    border-radius: 25px;
                    font-size: 18px;
                    color: #e9f4fb;
                    font-weight: 700;
                    cursor: pointer;
                    outline: none;
                    &:hover{
                    border-color: #2691d9;
                    transition: .5s;
                }
        }
     }
`
export default LoginIn
