import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import Svg from '../images/download.png'
// import { useTranslation } from 'react-i18next'

const Navbar = () => {
    // const {t, i18n} = useTranslation()

    // function handleClick(lang){
    //     i18n.changeLanguage('en-US')

    // }

    return (
        <Section>
            <nav class="navbar navbar-expand-lg">
                <div class="container">
                    <a class="navbar-brand d-flex justify-content-center align-items-center" href="#">
                        <img src={Svg} alt="" width="70" />
                        <p className="mt-3 ms-2 text-center">KANNAS<br/><span>textile</span></p>
                    </a>
                    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
                    <i class="fas fa-bars fa-2x text-white"></i>
                    </button>
                    <div class="collapse navbar-collapse" id="navbarNavAltMarkup">

                        <div class="navbar-nav m-auto">
                            <a href="#1" className="">Дом</a>
                            <a href="#2" className="">О нас</a>
                            <a href="#3" className="">Услуги</a>
                            <a href="#4" className="">Контакт</a>
                            
                        </div>
                        {/* <Link to="/login" class="nav-login">Войти в систему</Link> */}
                    </div>
                    {/* <button onClick={() => handleClick('en')}>English</button>
                    <button onClick={() => handleClick('uz')}>Uzbek</button> */}
                    
                </div>
            </nav>
        </Section>
    )
}
const Section = styled.div`
    .navbar{
        background: #281800;
        padding: 0;
       .collapse{
        .nav-login{
            margin-right: 3em;
            font-size: 18px;
            color: white;
            /* text-decoration: none; */
            font-family: 'Andada Pro', serif;
        }
       }
    }
.navbar-brand{
    line-height: 0.8em;
    font-size: 16px;
    align-items: center;
    font-family: 'Exo', sans-serif;
    p{
        color: gray;
    }
} 
    .navbar-nav{
        a{
            /* margin-right: 3em; */
            padding: 0 2em;
            font-size: 18px;
            color: white;
            text-decoration: none;
            font-family: 'Andada Pro', serif;
            @media (max-width:990px){
                 line-height: 2.3em;
                 text-align: center; 
           }
        }
      
    }
`
export default Navbar
